import React from 'react';
import './App.css';
import ReactDOM from 'react-dom';
import ReactTreeApp from './ReactTreeApp';

import DialogContainer from './DialogContainer.js';
import DisplayCode from './DisplayCode.js'
import code from './code-playground.js';
import * as d3 from "d3";
// import { scaleLinear } from "d3-scale";
// import {scaleLog} from 'd3-scale';
// import {extent} from "d3-array";


import { range as d3range } from "d3-array";
import { randomNormal as d3randomNormal } from "d3-random";

const alphabet = [..."abcdefghijklmnopqrstuvwxyz"];

let getData = (totalPoints = 100) => {
  return (  
    d3range(totalPoints).map((d, i) => {
      return { x: d, y: d3randomNormal()(), name: `${alphabet[d]}-${i}` };
    })
  )
}

class D3Circles extends React.Component {
  constructor(props) { 
    super(props);
    this.timer = null;
    this.state = {
      data: []
    };
  }

  componentDidMount() {

    this.timer = setInterval(() => {
      this.setState({data: getData(3)});
     }, 100)
  }

  componentDidUpdate() {
    this.updateChart();
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  updateChart = () => {
    const { data } = this.state;
    const { width, height } = this.props;
    let colors = ["#ff0000", "#00ff00", "#0000ff", "#fbb13c", "#"];

    //scales
    const xScale = d3
      .scaleBand()
      .domain(d3.range(0, data.length))
      .range([0, width]);

    const yScale = d3
      .scaleLinear()
      .domain([-3, 3])
      .range([0, height]);

    // const xScale = d3
    //   .scaleLinear()
    //   .domain([0,1000])
    //   .range([0, 1000]);

    // const yScale = d3
    //   .scaleLinear()
    //   .domain([0, 500])
    //   .range([0, 500]);


//    this.state.data.map((item) => console.log(item))

    //create a selector and attach data to it
    let svgContainer = d3
      .select(this.svgEl)
//      .select('svg')
      .selectAll("rect")
      .data(this.state.data);



    svgContainer
      .append("circle")
      .attr('cx', 1000)
      .attr('cy', 500)  
      .attr('r', 5)
      .style('fill', 'blue')
      

    svgContainer
      .enter()
      .append("circle")
      .merge(svgContainer)
      .transition()
      .duration(3500)
      .attr("cx", d => xScale(d.x))
      .attr("cy", d => yScale(d.y)) 
      .attr("r", xScale.bandwidth())
      .style("fill", (d, i) => colors[i])
      .remove();



//todo: maybe this is a good time to create a very simple example to explore transitions


    // svgContainer
    //   .enter()
    //   .append("rect")
    //   .merge(svgContainer)
    //   .transition()
    //   .duration(500)
    //   .attr("x", d => xScale(d.x))
    //   .attr("y", d => yScale(d.y)) 
    //   .attr("width", 600)
    //   .attr("height", 600) 

    //   .style("fill", (d, i) => colors[i])
    //   .remove();

    //remove bars that are not related to existing data points
    // svgContainer.exit().remove();
  };

  render() {
    return (
      <div>
        <svg
          width={this.props.width}
          height={this.props.height}
          ref={el => (this.svgEl = el)}
        >
        </svg>
      </div>
    );
  }
}

class BasicD3Stuff extends React.Component {
  constructor(props) {
    super(props);
    this.flip = false;
  }

  componentDidMount() {
    this.onClick();
  }
  
  onClick = () => {
    if (this.flip) {
      this.updateStuff([ {x:10, y:120, r:5, c:'#ff0000'}, {x:60, y:120, r:10, c:'#ff0000'}, {x:110, y:120, r:15, c:'#ff0000'}, {x:160, y:120, r:20, c:'#ff0000'}]);
    }
    else {
      this.updateStuff([ {x:10, y:120, r:5, c:'#ff00ff'}, {x:60, y:120, r:10, c:'#ff00ff'}, {x:110, y:120, r:15, c:'#ff00ff'}, {x:160, y:120, r:20, c:'#ff00ff'},  {x:220, y:120, r:30, c:'#ff00ff'}]);
    }  
    this.flip = !this.flip; 
  }

  updateStuff(data) {
    let numbers = new Array(Math.floor(Math.random() * 10));

    // const temperatureData = [ {x:10, y:20, r:2}, {x:40, y:20, r:8}, {x:70, y:30, r:20}, {x:100, y:40, r:8}, {x:130, y:50, r:15} ]
    let x = d3.select(this.refs.temperatures)
      .selectAll('circle')
      .data(data);

    x 
      .enter()
      .append("circle")
      .merge(x)
      .transition()
        // .delay((td) => td.r * 150)
        .duration(1000)
        .attr("cx", (td) => td.x)
        .attr("cy", (td) => td.y) 
        .attr("r", (td) => td.r)
        .style("fill", (td) => td.c)
    x.exit().remove();    
  }

  render() {
    return (
      <>
        <svg width={this.props.width} height={this.props.height} ref="temperatures"></svg>
        <button onClick={this.onClick}>New Data</button>
      </>
    )
  }
}

class DrawStuff extends React.Component {

  constructor(props) {
    super(props);
    this.myData = [{x:0, y:0, r:0}, {x:0, y:0, r:0}];
    this.drawStuff = this.drawStuff.bind(this);
    this.timer = null;
  }
  
  componentDidMount() {
    this.timer = setInterval(() => {
      this.drawStuff();
     }, 0)
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  drawStuff() { 
    //update
    let svgContainer = d3.select('svg')
      .selectAll('circle')
      .data(this.myData);


    //this is for html elements that already exist - like divs in original example  
    // x.transition()
    //   .ease(d3.easeLinear)
    //   .duration(1000)
    //   .style('fill', '#00ff00'); //green
 
    //enter
    svgContainer
     .enter() 
     .append('circle')
     .attr("cx", (d) => d.x)
     .attr("cy", (d) => d.y)
     .attr("r", (d) => d.r)
     .style('fill', 'red') //green

     .transition()
     .attr("transform", (d) => `translate(${d.x+1000}, ${d.y+500})`)
     .attr("r", (d) => d.r + 500) 
     .duration(2000)
     .style('fill', 'blue') //red

     .transition()
     .attr("transform", (d) => `translate(${d.x-1000}, ${d.y-500})`)
     .attr("r", (d) => d.r) 
     .duration(2000)
     .style('fill', 'red') //red


     .remove() 
     
    //exit
    // svgContainer.exit()
    // .transition()
    // .duration(2000)
    // .style('background-color', '#ffffff') //white
    // .remove();  
  }

  render() {
    return (
      <>
        <svg width={this.props.width} height={this.props.height} id='content'>
        </svg>

      </>
    )
  }
}

class D3Example extends React.Component {
    constructor(props) {
    super(props);
    this.goBack = this.goBack.bind(this);
    this.openDisplayCode = this.openDisplayCode.bind(this);
    this.onCancelDisplayCode = this.onCancelDisplayCode.bind(this);
    this.state = {
      showCode: false,
      data: [12, 5, 6, 6, 9, 10],
      width: 700,
      height: 500,
      id: 'root'
    };
  }

  goBack() {
    ReactDOM.render(<ReactTreeApp/>, document.getElementById('root'));
  }

  openDisplayCode() {
    this.setState({showCode: true});
  }

  onCancelDisplayCode() {
    this.setState({showCode: false});
  }

  render() {

    let screenWidth = window.screen.width;
    let screenHeight = window.screen.height;


    return (
      <div style={{height: '100%', position: 'absolute', width: '100%'}}>
        <div style={{height: '5%', textAlign: 'center', backgroundColor: 'lightGreen', paddingTop: '15px', paddingBottom: '15px'}}>
          <span><button onClick={this.goBack}>Back</button><label style={{fontSize: '24pt', marginLeft: '50px', marginRight: '50px'}}>D3</label><button onClick={this.openDisplayCode}>Code</button></span>
        </div>
        <DialogContainer show={this.state.showCode} onCancel={this.onCancelDisplayCode} dialogContent={<DisplayCode 
          code={code} onCancel={this.onCancelLogout}/>}
          width='1000px' height='700px' title={'D3Example.js'}>
        </DialogContainer>

        <div style={{height: '90%', position: 'absolute', width: '100%'}}>
  
          <D3Circles width={screenWidth} height={screenHeight}>  </D3Circles>
          {/* <BasicD3Stuff width={screenWidth} height={600}></BasicD3Stuff> */}
          {/* <BasicD3Stuff2 width={screenWidth} height={600}></BasicD3Stuff2> */}

          {/* <DrawStuff width={screenWidth} height={screenHeight}></DrawStuff> */}

        </div>    

     </div>
    )

  }
}



export default D3Example;